'use client';

import React, { useState, useEffect } from 'react';
import { useAppContext } from '@/context/AppContext';
import { useRouter } from 'next/navigation';
import Header from '@/components/Header';
import Footer from '@/components/Footer';
import ProfileCard from '@/components/AccountComponents/ProfileCard';
import ProfileForm from '@/components/AccountComponents/ProfileForm';
import OrderHistory from '@/components/AccountComponents/OrderHistory';
import NotificationPreferences from '@/components/AccountComponents/NotificationPreferences';
import SidebarNavigation from '@/components/AccountComponents/SidebarNavigation';

const AccountPage = () => {
  const { user, token, loading: contextLoading, fetchUserProfile } = useAppContext();
  const [activeSection, setActiveSection] = useState('profile');
  const [profileData, setProfileData] = useState<any>({});
  const [isEditing, setIsEditing] = useState(false);
  const [loading, setLoading] = useState(false);
  
  const router = useRouter();

  useEffect(() => {
    if (!token) {
      const currentPath = typeof window !== 'undefined' ? window.location.pathname : '/';
      router.push(`/auth?redirect=${encodeURIComponent(currentPath)}`);
    } else {
      // Fetch updated user profile when component mounts
      fetchUserProfile();
    }
  }, [token]);

  useEffect(() => {
    if (user) {
      setProfileData({
        name: user.name || '',
        email: user.email || '',
        phone: user.phone || '',
        address: user.address || '',
        city: user.city || '',
        state: user.state || '',
        country: user.country || '',
        postal_code: user.postal_code || '',
        date_of_birth: user.date_of_birth || '',
        gender: user.gender || ''
      });
    }
  }, [user]);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setProfileData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSaveProfile = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    try {
      const response = await fetch('/api/profile', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(profileData)
      });

      const result = await response.json();

      if (result.success) {
        setIsEditing(false);
        // Refetch profile to update all context values
        await fetchUserProfile();
      } else {
        console.error('Failed to update profile:', result.error);
      }
    } catch (error) {
      console.error('Profile update error:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleCancelEdit = () => {
    setIsEditing(false);
    // Reset form to original values
    setProfileData({
      name: user.name || '',
      email: user.email || '',
      phone: user.phone || '',
      address: user.address || '',
      city: user.city || '',
      state: user.state || '',
      country: user.country || '',
      postal_code: user.postal_code || '',
      date_of_birth: user.date_of_birth || '',
      gender: user.gender || ''
    });
  };

  const handleViewOrders = () => {
    router.push('/orders');
  };

  if (contextLoading.user) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-gray-50 to-gray-100">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-amber-600 mx-auto"></div>
          <p className="mt-4 text-gray-600 font-medium">Loading your profile...</p>
        </div>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-gray-50 to-gray-100">
        <div className="text-center p-8 bg-white rounded-2xl shadow-xl max-w-md border border-gray-100">
          <div className="mx-auto flex items-center justify-center h-16 w-16 rounded-full bg-red-100 mb-6">
            <svg className="h-8 w-8 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2z"></path>
            </svg>
          </div>
          <h2 className="text-2xl font-bold text-gray-900 mb-3">Access Required</h2>
          <p className="text-gray-600 mb-6">Please log in to access your account information.</p>
          <button
            className="px-6 py-3 bg-gradient-to-r from-amber-500 to-amber-600 text-white rounded-lg hover:from-amber-600 hover:to-amber-700 transition-all duration-300 shadow-lg font-medium"
            onClick={() => router.push('/auth')}
          >
            Sign In to Your Account
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100">
      <Header activePage="account" />

      <main className="container mx-auto px-4 py-8">
        <div className="max-w-7xl mx-auto">
          <div className="flex flex-col lg:flex-row gap-8">
            {/* Sidebar Navigation */}
            <div className="lg:w-1/4">
              <SidebarNavigation 
                activeSection={activeSection} 
                setActiveSection={setActiveSection} 
              />
            </div>

            {/* Main Content */}
            <div className="lg:w-3/4">
              {activeSection === 'profile' && (
                <>
                  {isEditing ? (
                    <ProfileForm
                      profileData={profileData}
                      onChange={handleInputChange}
                      onSave={handleSaveProfile}
                      onCancel={handleCancelEdit}
                      loading={loading}
                    />
                  ) : (
                    <ProfileCard
                      user={user}
                      onEditClick={() => setIsEditing(true)}
                      onViewOrders={handleViewOrders}
                    />
                  )}
                </>
              )}

              {activeSection === 'orders' && (
                <OrderHistory orders={user.orders} />
              )}

              {activeSection === 'notifications' && (
                <NotificationPreferences
                  preferences={user.notification_preferences || {}}
                  onSave={(prefs) => console.log('Saving preferences:', prefs)}
                  loading={false}
                />
              )}

              {activeSection === 'security' && (
                <div className="bg-white rounded-2xl shadow-lg p-8">
                  <h2 className="text-2xl font-bold text-gray-900 mb-6 pb-2 border-b border-gray-200">Security Settings</h2>
                  
                  <div className="space-y-6">
                    <div className="p-6 bg-gray-50 rounded-xl">
                      <div className="flex items-center">
                        <div className="mr-4 p-3 bg-amber-100 rounded-full">
                          <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-amber-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2zm10-10V7a4 4 0 00-8 0v4h8z" />
                          </svg>
                        </div>
                        <div>
                          <h3 className="font-bold text-gray-900">Change Password</h3>
                          <p className="text-sm text-gray-600 mt-1">Last updated: {user.created_at ? new Date(user.created_at).toLocaleDateString() : 'N/A'}</p>
                        </div>
                      </div>
                      <button className="mt-4 px-6 py-3 bg-gradient-to-r from-amber-500 to-amber-600 text-white rounded-lg hover:from-amber-600 hover:to-amber-700 transition-all duration-300 font-medium">
                        Change Password
                      </button>
                    </div>

                    <div className="p-6 bg-gray-50 rounded-xl">
                      <div className="flex items-center">
                        <div className="mr-4 p-3 bg-blue-100 rounded-full">
                          <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-blue-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2zm10-10V7a4 4 0 00-8 0v4h8z" />
                          </svg>
                        </div>
                        <div>
                          <h3 className="font-bold text-gray-900">Two-Factor Authentication</h3>
                          <p className="text-sm text-gray-600 mt-1">Not enabled</p>
                        </div>
                      </div>
                      <button className="mt-4 px-6 py-3 bg-gradient-to-r from-gray-300 to-gray-400 text-gray-800 rounded-lg hover:from-gray-400 hover:to-gray-500 transition-all duration-300 font-medium">
                        Enable 2FA
                      </button>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </main>

      <Footer />
    </div>
  );
};

export default AccountPage;